.slider {
	margin: 0;
	padding: 0;

	list-style-type: none;

	&__item {
		position: relative;
		flex-shrink: 0;
		max-width: 288px;
		width: 100%;
		margin: 0 8px;
		padding: 16px;
		padding-top: 128px;

		background: linear-gradient(180deg, #FBFBFB 0%, #F1F1F1 100%);
		border-radius: 32px;
	}

	&__img {
		position: absolute;
		top: -96px;
		left: 50%;
		transform: translateX(-50%);

		display: block;
		width: 224px;
		height: 224px;
	}

	&__title {
		margin: 0;
		margin-bottom: 8px;

		font-size: $font-size-l;
		line-height: 1.33333em;
		font-weight: 800;
		color: $text-color-secondary;

		&--short {
			max-width: 160px;
		}
	}

	&__text {
		margin: 0;
		margin-bottom: 16px;
		padding-right: 10px;
	}

	&__button {

	}
}
